import SysHeader from "../../../component/SysHeader";
import SysMain from "../../../component/SysMain";
import {Left, Plus} from "@icon-park/react";
import React, {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import SysPopup from "../../../component/SysPopup";
import '../../../css/app/health/healthMain.css'
import SysTab from "../../../component/SysTab";
import SysCard from "../../../component/SysCard";
import SysEmpty from "../../../component/SysEmpty";
import SysInput from "../../../component/SysInput";
import SysSelect from "../../../component/SysSelect";
import SysButton from "../../../component/SysButton";
import {isBlank} from "../../../js/StringUtils";
import {sysToast} from "../../../js/MessageUtils";
import {addData, delDataById, getDataByType} from "../../../js/DataUtils";
import SysTextEdit from "../../../component/SysTextEdit";
import {sysConfirm} from "../../../js/PopupUtils";

function HealthHelp() {
    const navigate = useNavigate();
    let [showAdd, setShowAdd] = useState();
    let [defTab, setDefTab] = useState(0);
    let [hhContent, setHhContent] = useState("");
    let [hhTittle, setHhTittle] = useState("");
    let [hhType, setHhType] = useState("hhjq");
    let [hhAuthType, setHhAuthType] = useState("hhUnShare");
    let [tabContent, setTabContent] = useState(<SysEmpty/>);
    const headerOption = {
        left: <Left/>,
        leftClick: () => goPage("/app/health"),
        right: <Plus/>,
        rightClick: () => setShowAdd(true),
    };
    useEffect(() => {
        initData();
    }, []);
    return (
        <div>
            <SysHeader option={headerOption}></SysHeader>
            <SysMain className="top-main">
                <SysTab tabList={["hhjq-紧急", "hhyf-预防", "hhzl-治疗"]} defTab={defTab} setDefTab={setDefTab}
                        tabContent={tabContent} change={change}/>
                <SysPopup title="添加" show={showAdd} setShowPopup={setShowAdd}>
                    <SysInput id="hqName" title="标题" defaultValue={hhTittle} setDefaultValue={setHhTittle}/>
                    <SysSelect id="hqType" title="类型" defaultValue={hhType} setDefaultValue={setHhType}
                               options={["hhjq-紧急", "hhyf-预防", "hhzl-治疗"]}/>
                    <SysSelect id="hqType" title="权限" defaultValue={hhAuthType} setDefaultValue={setHhAuthType}
                               options={["hhUnShare-仅自己可看", "hhSysShare-系统内可看", "hhSyncBlog-同步到博客"]}/>
                    <SysTextEdit title="内容" textContent={hhContent} setTextContent={setHhContent}/>
                    <SysButton text="保存" click={() => addHh()}/>
                </SysPopup>
            </SysMain>
        </div>
    );

    function goPage(page) {
        navigate(page);
    }

    function change(index) {
        setDefTab(index);
        setTabContent(<SysEmpty text="加载中..."/>);
        let tabList = ["hhjq-紧急", "hhyf-预防", "hhzl-治疗"];
        getHHByType(tabList[index].split("-")[0]);
    }

    function addHh() {
        if (isBlank(hhTittle) || isBlank(hhContent)) {
            sysToast("存在空项", "warn");
            return;
        }
        let hhData = {
            hhTittle: hhTittle,
            hhType: hhType,
            hhAuthType: hhAuthType,
            hhContent: hhContent
        }
        addData("kb-life-health-hh-" + hhType, hhData).then(addStatus => {
            if (addStatus) {
                sysToast("添加成功", "success");
                setShowAdd(false);
                getHHByType(hhType);
            }
        });
    }

    function toDelHh(dataId, dataType) {
        sysConfirm({
            confirm: () => delHh(dataId, dataType)
        });
    }

    function delHh(dataId, dataType) {
        delDataById(dataId).then(() => {
            getHHByType(dataType);
        });
    }

    function editHh() {

    }

    function showHh() {

    }

    function initData() {
        getHHByType("hhjq");
    }

    function getHHByType(type) {
        getDataByType("kb-life-health-hh-" + type).then(resData => {
            let hhContentList = [];
            resData.forEach((item, index) => {
                let itemObj = JSON.parse(item.data);
                hhContentList.push(
                    <SysCard
                        key={itemObj.hhTittle + index}
                        click={() => {
                        }}
                        title={itemObj.hhTittle}
                        content={itemObj.hhContent}
                        operateLeftMenu={<i style={{color: 'var(--primary-color)'}} className="fa fa-edit"></i>}
                        operateLeftMenuClick={() => {
                        }}
                        operateRightMenu={<i style={{color: 'red'}} className="fa fa-remove"></i>}
                        operateRightMenuClick={() => toDelHh(item.dataId, type)}/>);
            })
            if (hhContentList.length !== 0) {
                setTabContent(hhContentList);
            } else {
                setTabContent(<SysEmpty/>);
            }
        })
    }
}

export default HealthHelp;