import {closeSysLoading, showSysLoading, sysToast} from "./MessageUtils";
import {baseUrl} from "./RequestApi";
import {getLocalText} from "./Language";
import {getLocalAccount, getLocalAuthCode, toLoginPage} from "./UserUtils";
import {isBlank, isUndefined} from "./StringUtils";

export function requestUtils(name, type, url, param, loading) {
    if (isUndefined(loading)) {
        loading = false;
    }
    if (loading === true) {
        showSysLoading();
    }
    let cloudModel = localStorage.getItem("cloudModel");
    if (cloudModel) {
        return handleCloudData(name, type, url, param);
    } else {
        return handleLocalData(name, type, param);
    }
}

function handleLocalData(name, type, param) {
    if (isBlank(param)) {
        param = {};
    }
    let authToken = localStorage.getItem("authToken");
    var localAuthCode = getLocalAuthCode();
    var localAccount = getLocalAccount();
    if (name === "检查用户") {
        return new Promise(function (resolve, reject) {
            if (isUndefined(authToken) || authToken !== localAuthCode) {
                toLoginPage();
            } else {
                resolve(true);
            }
            closeSysLoading();
        });
    }
    if (name === "发送验证码") {
        return new Promise(function (resolve, reject) {
            if (param.account === localAccount) {
                resolve(true);
            } else {
                sysToast("账户不存在", "error");
                reject(false);
            }
            closeSysLoading();
        })
    }
    if (name === "登录") {
        return new Promise(function (resolve, reject) {
            if (param.userEmail === localAccount && param.authCode === localAuthCode) {
                resolve({
                    authToken: localAuthCode,
                    refreshToken: localAuthCode,
                    authUid: localAccount,
                    account: localAccount
                });
            } else {
                sysToast("验证码不正确", "error");
                reject(false);
            }
            closeSysLoading();
        })
    }
    return new Promise(function (resolve, reject) {
        switch (type) {
            case "post":
                let dataId = new Date().getTime();
                localStorage.setItem(param.dataType + "#" + dataId, JSON.stringify(param));
                resolve(true);
                break;
            case "delete":
                localStorage.removeItem(name.split("@")[1]);
                resolve(true);
                break;
            case "put":
                break;
            case "get":
                let length = localStorage.length;
                let typeListData = [];
                for (let i = 0; i < length; i++) {
                    let key = localStorage.key(i);
                    let dataType = name.split("@")[1];
                    if (key.indexOf(dataType + "#") !== -1) {
                        let data = localStorage.getItem(key);
                        let repData = JSON.parse(data);
                        repData.dataId = key;
                        typeListData.push(repData);
                    }
                }
                resolve(typeListData);
                break;
            default:
                break;
        }
        closeSysLoading();
    });
}

function handleCloudData(name, type, url, param) {
    if (isBlank(param)) {
        param = {};
    }
    let header = {};
    // 增加统一认证 header
    let authToken = localStorage.getItem("authToken");
    let refreshToken = localStorage.getItem("refreshToken");
    let authUid = localStorage.getItem("authUid");
    if (authToken && refreshToken && authUid) {
        header.authToken = authToken;
        header.refreshToken = refreshToken;
        header.authUid = authUid;
    }
    return new Promise(function (resolve, reject) {
        switch (type) {
            case "post":
                header['Content-Type'] = 'application/json';
                fetch(url, {
                    method: 'post',
                    body: JSON.stringify(param),
                    headers: header
                }).then(res => {
                    res.json().then((res) => {
                        let success = handleRequestStatus(url, res);
                        if (success) {
                            closeSysLoading();
                            resolve(res.data);
                        } else {
                            closeSysLoading();
                        }
                    });
                }).catch(function (error) {
                    reject(error);
                    closeSysLoading();
                    handleFailRequest();
                });
                break;
            case "delete":
                header['Content-Type'] = 'application/json';
                fetch(url, {
                    method: 'delete',
                    headers: header
                }).then(res => {
                    res.json().then((res) => {
                        let success = handleRequestStatus(url, res);
                        if (success) {
                            closeSysLoading();
                            resolve(res.data);
                        } else {
                            closeSysLoading();
                        }
                    });
                }).catch(function (error) {
                    reject(error);
                    closeSysLoading();
                    handleFailRequest();
                });
                break;
            case "put":
                header['Content-Type'] = 'application/json';
                fetch(url, {
                    method: 'put',
                    body: JSON.stringify(param),
                    headers: header
                }).then(res => {
                    res.json().then((res) => {
                        let success = handleRequestStatus(url, res);
                        if (success) {
                            closeSysLoading();
                            resolve(res.data);
                        } else {
                            closeSysLoading();
                        }
                    });
                }).catch(function (error) {
                    reject(error);
                    closeSysLoading();
                    handleFailRequest();
                });
                break;
            case "get":
                fetch(url, {
                    headers: header
                }).then(res => {
                    res.json().then((res) => {
                        let success = handleRequestStatus(url, res);
                        if (success) {
                            closeSysLoading();
                            resolve(res.data);
                        } else {
                            closeSysLoading();
                        }
                    });
                }).catch(function (error) {
                    reject(error);
                    closeSysLoading();
                    handleFailRequest();
                });
                break;
            default:
                break;
        }
    });
}

function handleRequestStatus(url, res) {
    let resStatus = res.code;
    if (resStatus === 200) {
        return true;
    } else if (resStatus === 401) {
        toLoginPage();
    } else if (resStatus === 400) {
        sysToast(res.msg, "warn");
    } else {
        if (url === baseUrl + "/auth/check") {
            toLoginPage();
        } else {
            handleFailRequest();
        }
    }
    return false;
}

export function handleFailRequest(fromPage) {
    sysToast(getLocalText("net_fail"), "error");
}