import {isPhone} from "../js/Device";
import {handleUndefined} from "../js/StringUtils";

function SysInput(props) {
    let title = handleUndefined(props.title);
    let defaultValue = handleUndefined(props.defaultValue);
    let setDefaultValue = handleUndefined(props.setDefaultValue, undefined);
    let width = handleUndefined(props.width);
    let height = handleUndefined(props.height);
    let id = handleUndefined(props.id);
    let inputType = handleUndefined(props.type);
    let inputPattern = handleUndefined(props.pattern);
    let inputPlaceholder = handleUndefined(props.placeholder);
    let show = handleUndefined(props.show, true);
    if (inputType === "number" && isPhone()) {
        inputPattern = "\\d*";
    }
    let inputHtml =
        <div>
            <div className="sys-form-title"
                 style={{display: show ? "block" : "none", height: height, width: width}}>{title}</div>
            <input className="sys-input" id={id} placeholder={inputPlaceholder} type={inputType}
                   pattern={inputPattern}
                   value={defaultValue}
                   onChange={(e) => {
                       setDefaultValue(e.target.value)
                   }}
                   style={{display: show ? "block" : "none", height: height, width: width}}/>
        </div>;
    return (inputHtml);
}

export default SysInput;