export function autoTheme() {
    autoDark();
    document.documentElement.style.setProperty('--animate-duration', '0.5s');
}

export function autoDark() {
    //获取media信息对象
    let media = window.matchMedia('(prefers-color-scheme:dark)');
    if (media.matches) {
        setDark();
    } else {
        setLight();
    }
    //监听切换
    let callback = e => {
        let prefersDarkMode = e.matches;
        if (prefersDarkMode) {
            setDark();
        } else {
            setLight();
        }
    }
    if (typeof media.addEventListener === 'function') {
        media.addEventListener('change', callback);
    } else if (typeof media.addListener === 'function') {
        media.addListener(callback);
    }
}

export function setDark() {
    document.documentElement.setAttribute('theme', 'dark');
    document.documentElement.setAttribute('data-prefers-color-scheme', 'dark')
    localStorage.setItem("dark", true);
}

export function setLight() {
    document.documentElement.removeAttribute('theme');
    document.documentElement.removeAttribute('data-prefers-color-scheme')
    localStorage.setItem("dark", false);
}

export function doSound(type) {
    let soundPath = "";
    switch (type) {
        case "done":
            soundPath = "https://ppt-mp3cdn.hrxz.com/d/file/filemp3/hrxz.com-3snzezt3j3k23482.mp3";
            break;
        default:
            soundPath = "";
    }
    let audio = new Audio(soundPath);
    audio.play().then(audio.remove());
}
