import React from "react";
import {Skeleton} from "antd-mobile";

function SysListSkeleton() {
    const iconSkeleton = {
        margin: '5px',
        width: '50px',
        height: '50px',
        borderRadius: '50px'
    }
    const contentSkeleton = {
        margin: '5px', width: 'calc(100vw - 10px - 60px - 26px)', height: '50px'
    }
    let listSkeletonItem =
        <div className="sys-list-item">
            <Skeleton animated style={iconSkeleton}/>
            <Skeleton animated style={contentSkeleton}/>
        </div>;
    return (
        <div>
            {listSkeletonItem}
            {listSkeletonItem}
            {listSkeletonItem}
        </div>
    );
}

export default SysListSkeleton;