import {handleUndefined} from "../js/StringUtils";

function SysSelect(props) {
    let defaultValue = handleUndefined(props.defaultValue);
    let setDefaultValue = handleUndefined(props.setDefaultValue, undefined);
    let id = handleUndefined(props.id, "sysSel");
    let title = handleUndefined(props.title, "请选择");
    let options = handleUndefined(props.options, []);

    return (
        <div className="sys-select">
            <input id={id} type="hidden" value={defaultValue}/>
            <div className="sys-form-title">{title}</div>
            <div className="sys-select-option-list">
                {options.map((item, index) => (
                    <div
                        className={(defaultValue === item.split("-")[0]) ? "sys-select-option-active" : "sys-select-option"}
                        key={item.split("-")[0]}
                        onClick={() => {
                            setDefaultValue(item.split("-")[0])
                        }}>{item.split("-")[1]}</div>
                ))}
            </div>
        </div>
    );
}

export default SysSelect;