import SysHeader from "../../../component/SysHeader";
import React, {useEffect, useState} from "react";
import SysMain from "../../../component/SysMain";
import {useNavigate} from "react-router-dom";
import SysListItem from "../../../component/SysListItem";
import SysFooter from "../../../component/SysFooter";
import SysPopup from "../../../component/SysPopup";
import {Check, DegreeHat, Jump, Left, Round, Setting} from "@icon-park/react";
import SysTag from "../../../component/SysTag";
import {addData, delDataById, getDataByType} from "../../../js/DataUtils";
import {getNowDateStr, getNowWeek, isWorkDay} from "../../../js/DateUtils";
import SysPullToRefresh from "../../../component/SysPullToRefresh";
import SysEmpty from "../../../component/SysEmpty";
import SysListSkeleton from "../../../component/SysListSkeleton";
import {sysToast} from "../../../js/MessageUtils";
import {dataCircleChart} from "../../../js/DataView";


function ToDoMain() {
    const [showUndo, setShowUndo] = useState(false);
    const [undoData, setUndoData] = useState(<SysListSkeleton/>);
    const navigate = useNavigate();
    const headerOption = {
        left: <Left/>,
        leftClick: () => goPage("/"),
        right: <Setting/>,
        rightClick: () => goTodoOption(),
    };
    useEffect(() => {
        initData();
    }, []);

    function initData() {
        let nowDateStr = getNowDateStr();
        let smNum = 0;
        let tSmNum = 0;
        let ysNum = 0;
        let tYsNum = 0;
        let ydNum = 0;
        let tYdNum = 0;
        let xxNum = 0;
        let tXxNum = 0;
        let xqNum = 0;
        let tXqNum = 0;
        let sjNum = 0;
        let tSjNum = 0;
        getTodoData("todo-item-" + nowDateStr).then(tTodoItem => {
            getTodoData("todo-item").then(todoItem => {
                tTodoItem.forEach((item, index) => {
                    let tTodoData = JSON.parse(item.data);
                    switch (tTodoData.todoType) {
                        case "sm":
                            tSmNum++;
                            break;
                        case "ys":
                            tYsNum++;
                            break;
                        case "yd":
                            tYdNum++;
                            break;
                        case "xx":
                            tXxNum++;
                            break;
                        case "xq":
                            tXqNum++;
                            break;
                        case "sj":
                            tSjNum++;
                            break;
                        default:
                            break;
                    }
                })

                todoItem.forEach((item, index) => {
                    let todoData = JSON.parse(item.data);
                    let timeTypeCheck = checkTimeType(todoData.todoTimeType);
                    switch (todoData.todoType) {
                        case "sm":
                            if (timeTypeCheck) {
                                smNum++;
                            }
                            break;
                        case "ys":
                            if (timeTypeCheck) {
                                ysNum++;
                            }
                            break;
                        case "yd":
                            if (timeTypeCheck) {
                                ydNum++;
                            }
                            break;
                        case "xx":
                            if (timeTypeCheck) {
                                xxNum++;
                            }
                            break;
                        case "xq":
                            if (timeTypeCheck) {
                                xqNum++;
                            }
                            break;
                        case "sj":
                            if (timeTypeCheck) {
                                sjNum++;
                            }
                            break;
                        default:
                            break;
                    }
                })
                let smV = (tSmNum / smNum).toFixed(1);
                console.log(smV);
                if (tSmNum === 0 && smNum === 0) {
                    smV = 1;
                }
                let ysV = (tYsNum / ysNum).toFixed(1);
                if (tYsNum === 0 && ysNum === 0) {
                    ysV = 1;
                }
                let ydV = (tYdNum / ydNum).toFixed(1);
                if (tYdNum === 0 && ydNum === 0) {
                    ydV = 1;
                }
                setSeaChart(smV, ysV, ydV);
                let xxV = (tXxNum / xxNum).toFixed(1);
                if (tXxNum === 0 && xxNum === 0) {
                    xxV = 1;
                }
                let xqV = (tXqNum / xqNum).toFixed(1);
                if (tXqNum === 0 && xqNum === 0) {
                    xqV = 1;
                }
                let sjV = (tSjNum / sjNum).toFixed(1);
                if (tSjNum === 0 && sjNum === 0) {
                    sjV = 1;
                }
                setSpwChart(xxV, xqV, sjV);
            });
        });
    }

    function setSeaChart(smV, ysV, ydV) {
        let dataV = [ydV, ysV, smV];
        let imgs =
            [
                'data:image/svg+xml;base64,',
                'data:image/svg+xml;base64,',
                'data:image/svg+xml;base64,'
            ];
        let colors = ['#aaff00', '#ffff00', '#aa00ff'];
        dataCircleChart("bContainer", dataV, imgs, colors);
    }

    function setSpwChart(xxV, xqV, sjV) {
        let dataV = [sjV, xqV, xxV];
        let imgs =
            [
                'data:image/svg+xml;base64,',
                'data:image/svg+xml;base64,',
                'data:image/svg+xml;base64,'
            ];
        let colors = ['#007bff', '#ff8400', '#ff0000'];
        dataCircleChart("sContainer", dataV, imgs, colors);
    }

    function addTodayToDo(type, dataId, e) {
        let nowDateStr = getNowDateStr();
        let data = {todoId: dataId, todoType: type};
        addData("todo-item-" + nowDateStr, data).then(res => {
            if (res) {
                sysToast("打卡成功", "success");
                getUndoItem().then();
                initData();
            }
        });
    }

    function delTodayToDo(dataId) {
        delDataById(dataId).then(res => {
            if (res) {
                getUndoItem().then();
                initData();
            }
        })
    }

    function checkTimeType(todoTimeType) {
        let todoTimeTypeName = getTodoTypeName(todoTimeType);
        if (isWorkDay()) {
            if (todoTimeTypeName === "周六" || todoTimeTypeName === "周日") {
                return false;
            }
        } else {
            if (todoTimeTypeName === "工作日") {
                return false;
            }
            if (todoTimeTypeName === "周日" && getNowWeek() !== 0) {
                return false;
            }
            if (todoTimeTypeName === "周六" && getNowWeek() !== 6) {
                return false;
            }
        }
        return true;
    }

    function getTodoTypeName(todoTimeType) {
        switch (todoTimeType) {
            case "mt":
                return "每天";
            case "gzr":
                return "工作日";
            case "z6":
                return "周六";
            case "zr":
                return "周日";
            default:
                return "";
        }
    }

    function getUndoItem() {
        return new Promise(function (resolve, reject) {
            let nowDateStr = getNowDateStr();
            setUndoData(<SysListSkeleton/>);
            getTodoData("todo-item").then(todoOption => {
                getTodoData("todo-item-" + nowDateStr).then(tTodoOption => {
                    let tDoItemArray = [];
                    let tDoItemMap = new Map();
                    tTodoOption.forEach((item, index) => {
                        let todoData = JSON.parse(item.data);
                        tDoItemArray.push(todoData.todoId);
                        tDoItemMap.set(todoData.todoId, item.dataId);
                    })
                    let doItemArray = [];
                    todoOption.forEach((item, index) => {
                        let todoData = JSON.parse(item.data);
                        let todoTimeTypeName = getTodoTypeName(todoData.todoTimeType);
                        if (!checkTimeType(todoData.todoTimeType)) {
                            return;
                        }
                        if (tDoItemArray.indexOf(item.dataId) === -1) {
                            doItemArray.push(<SysListItem key={item.dataId}
                                                          icon={<Round fill="red"/>}
                                                          iconClick={(e) => addTodayToDo(todoData.todoType, item.dataId, e)}
                                                          iconCheck={false}
                                                          content={
                                                              <span>{todoData.todoName + " " + todoTimeTypeName + " " + todoData.todoTime}</span>}/>);
                        } else {
                            doItemArray.push(<SysListItem key={todoData.todoName + index}
                                                          icon={<Check fill="var(--primary-color)"/>}
                                                          iconCheck={true}
                                                          iconClick={() => delTodayToDo(tDoItemMap.get(item.dataId))}
                                                          content={
                                                              <span>{todoData.todoName + " " + todoTimeTypeName + " " + todoData.todoTime}</span>}/>);
                        }
                    })
                    if (doItemArray.length > 0) {
                        setUndoData(doItemArray);
                    } else {
                        setUndoData(<SysEmpty/>);
                    }
                    resolve(true);
                });
            });
        });
    }

    function getTodoData(todoType) {
        return new Promise(function (resolve, reject) {
            getDataByType(todoType).then(resData => {
                let data = [];
                resData.forEach((item, index) => {
                    data.push(item);
                })
                resolve(data);
            });
        });
    }

    function goPage(page) {
        navigate(page);
    }

    function goTodoOption() {
        goPage("/app/todo/option");
    }

    return (
        <div>
            <SysHeader option={headerOption}/>
            <SysMain className="main">
                <SysPullToRefresh onRefresh={() => initData()}>
                    <SysTag tagName={<Jump/>}/>
                    <div style={{textAlign: 'center', padding: '10px'}}>
                        <canvas id="bContainer"></canvas>
                    </div>
                    <SysTag tagName={<DegreeHat/>}/>
                    <div style={{textAlign: 'center', padding: '10px'}}>
                        <canvas id="sContainer"></canvas>
                    </div>
                </SysPullToRefresh>
            </SysMain>
            <SysFooter>
                <SysPopup title="今日" show={showUndo} setShowPopup={setShowUndo}>
                    {undoData}
                </SysPopup>
                <div className="footer-menu" onClick={() => {
                    setUndoData(<SysListSkeleton></SysListSkeleton>)
                    setShowUndo(true);
                    getUndoItem().then();
                }}>
                    <Round fill="red"/>
                </div>
            </SysFooter>
        </div>
    );
}

export default ToDoMain;
