import {addDataApi, delDataApi, getTypeDataApi, updateDataApi} from "./RequestApi";
import {requestUtils} from "./RequestUtils";

export function addData(dataType, data) {
    let param = {
        dataType: dataType,
        data: JSON.stringify(data),
    }
    return new Promise(function (resolve) {
        requestUtils((new Date().getTime() + "@type"), "post", addDataApi, param).then(res => {
            resolve(res);
        })
    })
}

export function delDataById(dataId) {
    return new Promise(function (resolve) {
        requestUtils((new Date().getTime() + "@" + dataId), "delete", delDataApi + dataId).then(res => {
            resolve(res);
        })
    })
}

export function updateDataById(dataId, dataType, data) {
    let param = {
        dataId: dataId,
        dataType: dataType,
        data: data,
    }
    return new Promise(function (resolve) {
        requestUtils((new Date().getTime() + "@" + dataType), "put", updateDataApi, param).then(res => {
            resolve(res);
        })
    })
}


export function getDataByType(dataType) {
    return new Promise(function (resolve) {
        requestUtils((new Date().getTime() + "@" + dataType), "get", getTypeDataApi + dataType).then(res => {
            resolve(res);
        })
    })
}
