import {handleUndefined} from "../js/StringUtils";
import {SwipeAction} from "antd-mobile";
import React from "react";
import SysListSkeleton from "./SysListSkeleton";

function SysListItem(props) {
    let swipeMenu = handleUndefined(props.swipeMenu, false);
    let iconClick = handleUndefined(props.iconClick);
    let iconCheck = handleUndefined(props.iconCheck, false);
    let show = handleUndefined(props.show, true);
    let content = handleUndefined(props.content, <SysListSkeleton/>);
    let contentClick = handleUndefined(props.click, undefined);
    let rightActions = handleUndefined(props.swipeMenuList, []);
    let listItemHtml;
    if (swipeMenu) {
        listItemHtml =
            <SwipeAction rightActions={rightActions}>
                <div className="sys-list-item-content"
                     onClick={contentClick}>{content}</div>
            </SwipeAction>
    } else {
        listItemHtml =
            <div className="sys-list-item-content"
                 onClick={contentClick}>{content}</div>
    }
    return (
        <div style={{display: show ? "block" : "none"}}>
            <div className="sys-list-item">
                <div className={iconCheck ? "sys-list-item-icon active" : "sys-list-item-icon"} onClick={iconClick}>{props.icon}</div>
                {listItemHtml}
            </div>
        </div>
    )
}

export default SysListItem;