import {handleUndefined} from "../js/StringUtils";
import $ from 'jquery';

function SysTextEdit(props) {
    let id = handleUndefined(props.id, "");
    let title = handleUndefined(props.title, "");
    let placeholder = handleUndefined(props.placeholder, "");
    let textContent = handleUndefined(props.textContent, "");
    let setTextContent = handleUndefined(props.setTextContent, "");
    let textareaHtml =
        <div>
            <div className="sys-form-title">{title}</div>
            <textarea className="sys-input-text" id={id} placeholder={placeholder}
                      onChange={(e) => {
                          setTextContent(e.target.value);
                          previewText(e.target.value);
                      }}>
                {textContent}
            </textarea>
            <div className="sys-form-title">预览</div>
            <div id="textPre" className="sys-input-pre"></div>
        </div>;
    return (textareaHtml);

    function previewText(content) {
        let contentArray = content.split("\n");
        let contentPre = "";
        for (let i in contentArray) {
            let rowText = contentArray[i];
            let t1 = rowText.startsWith("# ");
            let t2 = rowText.startsWith("## ");
            let t3 = rowText.startsWith("### ");
            let p = rowText.startsWith("* ");
            if (t1) {
                rowText = "<h1>" + rowText.substr(1, rowText.length - 1) + "</h1>";
            } else if (t2) {
                rowText = "<h2>" + rowText.substr(2, rowText.length - 1) + "</h2>";
            } else if (t3) {
                rowText = "<h3>" + rowText.substr(3, rowText.length - 1) + "</h3>";
            } else if (p) {
                rowText = "<li>" + rowText.substr(1, rowText.length - 1) + "</li>";
            } else {
                rowText = "<div>" + rowText + "</div>";
            }
            contentPre += rowText;
        }
        $("#textPre").html(contentPre);
    }
}

export default SysTextEdit;