export function getNowDateStr() {
    return new Date().getFullYear() + "" + handlerDateNumStr(new Date().getMonth() + 1) + "" + handlerDateNumStr(new Date().getDate());
}

export function handlerDateNumStr(dateNum) {
    return parseInt(dateNum) > 10 ? dateNum : "0" + dateNum;
}

export function getNowMonthDayStr() {
    return handlerDateNumStr(new Date().getMonth() + 1) + "-" + handlerDateNumStr(new Date().getDate());
}

export function getNowWeek() {
    return new Date().getDay();
}

export function isWorkDay() {
    if (getNowWeek() !== 6 && getNowWeek() !== 0) {
        return true;
    }
    return false;
}

export function isWeekSingle() {
    if (getNowWeek() !== "2" && getNowWeek() !== "4" && getNowWeek() !== "6") {
        return true;
    }
    return false;
}