import SysHeader from "../../../component/SysHeader";
import SysMain from "../../../component/SysMain";
import SysFooter from "../../../component/SysFooter";
import {Brain, Chest, Gongfu, Left, MedicineChest, Plus} from "@icon-park/react";
import React, {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import SysPopup from "../../../component/SysPopup";
import '../../../css/app/health/healthMain.css'
import SysInput from "../../../component/SysInput";
import SysSelect from "../../../component/SysSelect";
import SysButton from "../../../component/SysButton";
import {addData, delDataById, getDataByType} from "../../../js/DataUtils";
import {isBlank} from "../../../js/StringUtils";
import {sysToast} from "../../../js/MessageUtils";
import SysListItem from "../../../component/SysListItem";
import SysListSkeleton from "../../../component/SysListSkeleton";
import {sysConfirm} from "../../../js/PopupUtils";
import $ from 'jquery';
import SysPullToRefresh from "../../../component/SysPullToRefresh";

function HealthMain() {
    const navigate = useNavigate();
    let [showAdd, setShowAdd] = useState(false);
    let [showHqInfo, setShowHqInfo] = useState(false);
    let [cHqDetail, setCHqDetail] = useState("");
    let [hqName, setHqName] = useState("");
    let [hqType, setHqType] = useState('hq_mind');
    const headerOption = {
        left: <Left/>,
        leftClick: () => goPage("/"),
        right: <Plus/>,
        rightClick: () => setShowAdd(true),
    };
    useEffect(() => {
        initData();
    }, []);
    return (
        <div>
            <SysHeader option={headerOption}></SysHeader>
            <SysMain className="main">
                <SysPullToRefresh onRefresh={() => initData()}>

                    <div className="b_top_div">
                        <div className="b_top_item"><span className="b_top_item_text">状态加载中...</span></div>
                    </div>
                    <div>
                        <div className="b_head">
                            <div className="b_head_mid" onClick={() => showHqDetail('hq_mind')}></div>
                        </div>
                        <div className="b_body">
                            <div className="b_body_left" onClick={() => showHqDetail('hq_do')}></div>
                            <div className="b_body_mid" onClick={() => showHqDetail('hq_body')}></div>
                            <div className="b_body_right" onClick={() => showHqDetail('hq_do')}></div>
                        </div>
                        <div className="b_foot">
                            <div className="b_foot_left" onClick={() => showHqDetail('hq_do')}></div>
                            <div className="b_foot_right" onClick={() => showHqDetail('hq_do')}></div>
                        </div>
                    </div>
                    <div className="b_bottom_div">
                    </div>
                    <SysPopup title="添加" show={showAdd} setShowPopup={setShowAdd}>
                        <SysInput id="hqName" title="名称" defaultValue={hqName} setDefaultValue={setHqName}/>
                        <SysSelect id="hqType" title="类型" defaultValue={hqType} setDefaultValue={setHqType}
                                   options={["hq_mind-头部", "hq_body-中部", "hq_do-肢体"]}/>
                        <SysButton text="保存" click={() => addHq()}/>
                    </SysPopup>
                    <SysPopup title="详情" show={showHqInfo} setShowPopup={setShowHqInfo}>
                        {cHqDetail}
                    </SysPopup>
                </SysPullToRefresh>
            </SysMain>
            <SysFooter>
                <div className="footer-menu" onClick={() => goPage("/app/health/help")}>
                    <MedicineChest/>
                </div>
            </SysFooter>
        </div>
    );

    function goPage(page) {
        navigate(page);
    }

    function addHq() {
        if (isBlank(hqName) || isBlank(hqType)) {
            sysToast("存在空项", "warn");
            return;
        }
        let hqData = {
            hqName: hqName,
            hqType: hqType
        }
        addData("health-hq-" + hqType, hqData).then(addStatus => {
            if (addStatus) {
                sysToast("记录成功，早日康复！", "success");
                initData();
                setShowAdd(false);
                setHqName("");
                setHqType("hq_mind");
            }
        });
    }

    function showHqDetail(hqType) {
        setCHqDetail(<SysListSkeleton/>);
        getDataByType("health-hq-" + hqType).then(resData => {
            let hqTypeData = [];
            resData.forEach((item) => {
                let hqData = JSON.parse(item.data);
                let name = hqData.hqName;
                let hqTypeIcon;
                if (hqType === "hq_mind") {
                    hqTypeIcon = <Brain/>;
                } else if (hqType === "hq_do") {
                    hqTypeIcon = <Gongfu/>;
                } else {
                    hqTypeIcon = <Chest/>;
                }
                let hqDataHtml =
                    <SysListItem content={name} icon={hqTypeIcon} swipeMenu={true} swipeMenuList={[
                        {
                            key: 'delete',
                            text: '去除',
                            color: 'danger',
                            onClick: async () => {
                                sysConfirm({
                                    confirm: function (e) {
                                        delDataById(item.dataId).then(res => {
                                            if (res) {
                                                sysToast("可喜可贺，努力保持！", "success");
                                                showHqDetail(hqType);
                                                initData();
                                            }
                                        });
                                    }
                                });
                            },
                        },
                    ]}/>;
                hqTypeData.push(hqDataHtml);
            })
            if (hqTypeData.length > 0) {
                setCHqDetail(hqTypeData);
                setShowHqInfo(true);
            } else {
                setShowHqInfo(false);
                sysToast("可喜可贺，非常健康！");
            }
        });
    }

    function initData() {
        let totalHqCount = 0;
        getDataByType("health-hq-hq_mind").then(resData => {
            let hqMindCount = resData.length;
            totalHqCount += hqMindCount;
            getDataByType("health-hq-hq_body").then(resData => {
                let hqBodyCount = resData.length;
                totalHqCount += hqBodyCount;
                getDataByType("health-hq-hq_do").then(resData => {
                    let hqDoCount = resData.length;
                    totalHqCount += hqDoCount;
                    let hqLevel = getHqLevel(totalHqCount).split("-");
                    $(".b_body_mid").css("box-shadow", "0 2px 12px 0 " + hqLevel[0]);
                    $(".b_head_mid").css("box-shadow", "0 2px 12px 0 " + hqLevel[0]);
                    $(".b_body_left").css("box-shadow", "0 2px 12px 0  " + hqLevel[0]);
                    $(".b_body_right").css("box-shadow", "0 2px 12px 0  " + hqLevel[0]);
                    $(".b_foot_left").css("box-shadow", "0 2px 12px 0  " + hqLevel[0]);
                    $(".b_foot_right").css("box-shadow", "0 2px 12px 0  " + hqLevel[0]);
                    $(".b_top_item_text").css("color", hqLevel[0]);
                    $(".b_top_item_text").text(hqLevel[1]);
                });
            });
        });
    }

    function getHqLevel(hqCount) {
        if (hqCount === 0) {
            return "#BDDD22-非常健康";
        } else if (hqCount > 0 && hqCount < 5) {
            $(".b_head_mid").css("box-shadow", "0 2px 12px 0 #008000d0");
            return "#789262-比较健康";
        } else if (hqCount >= 5 && hqCount < 50) {
            return "#FA8C35-不太健康";
        } else if (hqCount >= 50 && hqCount < 100) {
            return "#8C4356-比较严重";
        } else if (hqCount >= 100) {
            return "#FF2121-非常严重";
        }
    }
}

export default HealthMain;