import "../../css/login.css"
import React, {useState} from "react";
import {sysToast} from "../../js/MessageUtils";
import $ from 'jquery'
import {isEmail} from "../../js/StringUtils";
import {getLocalText} from "../../js/Language";
import SysButton from "../../component/SysButton";
import SysInput from "../../component/SysInput";
import {login, sendAuthCode} from "../../js/UserUtils";
import SysHeader from "../../component/SysHeader";
import {Left} from "@icon-park/react";


function LoginPage() {
    const [nextShow, setNextShow] = useState(false);
    const [loginEmail, setLoginEmail] = useState(localStorage.getItem("account"));
    const [authCode, setAuthCode] = useState(localStorage.getItem("account"));
    const headerOption = {
        left: <Left/>,
        leftClick: () => backSendCode(),
    };
    return (
        <div>
            <SysHeader show={nextShow} option={headerOption}/>
            <div className="login-div">
                <div className="login-logo">
                    <img src="logo.png" width="100px" height="100px" alt="logo"/>
                </div>
                <SysInput id="email" title={getLocalText('email_input')}
                          type="email"
                          defaultValue={loginEmail}
                          setDefaultValue={setLoginEmail}
                          show={!nextShow}/>
                <SysInput id="authCode" title={getLocalText('code_input')}
                          type="number"
                          defaultValue={authCode}
                          setDefaultValue={setAuthCode}
                          show={nextShow}/>
                <div className="line-row"></div>
                <SysButton text={getLocalText("send_code_btn_text")} show={!nextShow} click={() => sendLoginCode()}/>
                <SysButton text={getLocalText("login_btn_text")} show={nextShow} click={() => toLogin()}/>
            </div>
        </div>
    )

    function sendLoginCode() {
        let email = $("#email").val();
        if (email) {
            if (!isEmail(email)) {
                sysToast(getLocalText("email_invalid"), "warn");
            } else {
                sendAuthCode(email).then(res => {
                    setNextShow(true);
                })
            }
        } else {
            $("#email").focus();
            sysToast(getLocalText("email_empty"), "warn");
        }
    }

    function backSendCode() {
        setNextShow(false);
    }

    function toLogin() {
        let authCode = $("#authCode").val();
        if (authCode) {
            login($("#email").val(), authCode);
        } else {
            $("#authCode").focus();
            sysToast(getLocalText("auth_code_empty"), "warn");
        }
    }
}


export default LoginPage;