import './css/natureTheme.css';
import './css/common.css';
import '@icon-park/react/styles/index.css';
import {HashRouter as Router, Route, Routes} from "react-router-dom";
import Mine from "./jsx/user/Mine";
import {autoTheme} from "./js/NatureUtils";
import HomePage from "./jsx/home/HomePage";
import LoginPage from "./jsx/user/Login";
import ToDoMain from "./jsx/app/ToDo/ToDoMain";
import ToDoOption from "./jsx/app/ToDo/ToDoOption";
import SysFooterNav from "./component/SysFooterNav";
import React from "react";
import {AllApplication, Me} from "@icon-park/react";
import {checkUser} from "./js/UserUtils";
import HealthMain from "./jsx/app/Health/HealthMain";
import HealthHelp from "./jsx/app/Health/HealthHelp";

autoTheme();

checkUser();

function App() {
    const menus = [
        {name: <AllApplication size="24"/>, path: "/"},
        {name: <Me size="24"/>, path: "/user/mine"},
    ];
    return (
        <div>
            <Router>
                <Routes>
                    <Route path="/" element={<HomePage/>}/>
                    <Route path="/req/fail" element={<Mine/>}/>
                    <Route path="/user/mine" element={<Mine/>}/>
                    <Route path="/user/login" element={<LoginPage/>}/>
                    <Route path="/app/todo" element={<ToDoMain/>}/>
                    <Route path="/app/todo/option" element={<ToDoOption/>}/>
                    <Route path="/app/health" element={<HealthMain/>}/>
                    <Route path="/app/health/help" element={<HealthHelp/>}/>
                </Routes>
                <SysFooterNav menus={menus}/>
            </Router>
        </div>
    );
}

export default App;
