import {useNavigate} from "react-router-dom";
import {isNotBlank} from "../js/StringUtils";
import React from "react";

function SysMenuButton(props) {
    const navigate = useNavigate();

    function goPage() {
        if (isNotBlank(props.url)) {
            navigate(props.url);
        }
    }

    const btnHtml =
        <div className="sys-menu-btn" onClick={() => goPage()}>
            <div style={{fontSize: '30px'}}>{props.menuIcon}</div>
            <div style={{fontSize: '10px'}}>{props.menuText}</div>
        </div>;
    return (btnHtml);
}

export default SysMenuButton;