import {handleUndefined} from "../js/StringUtils";

function SysCard(props) {
    let operateLeftMenu = handleUndefined(props.operateLeftMenu, "");
    let operateLeftMenuClick = handleUndefined(props.operateLeftMenuClick, "");
    let operateRightMenu = handleUndefined(props.operateRightMenu, "");
    let operateRightMenuClick = handleUndefined(props.operateRightMenuClick, "");
    console.log(operateLeftMenu)
    return (
        <div>
            <div className="sys-card" onClick={handleUndefined(props.click)}>
                <div className="sys-card-title">{handleUndefined(props.title, "")}</div>
                <div className="sys-card-content">{handleUndefined(props.content, "")}</div>
                <div className="sys-card-operate">
                    <div className="sys-card-operate-item" onClick={operateLeftMenuClick}>{operateLeftMenu}</div>
                    <div className="sys-card-operate-item"
                         onClick={operateRightMenuClick}>{operateRightMenu}</div>
                </div>
            </div>
        </div>
    );
}

export default SysCard;