import {requestUtils} from "./RequestUtils";
import {checkUserApi, loginApi, logoutApi, sendAuthCodeApi} from "./RequestApi";

let curLoginPath = window.location.protocol + "//" + document.location.host + "/#/user/login";
let localAuthToken = "899111";
let localAccount = "local@163.com";

export function checkUser() {
    if (window.location.href !== curLoginPath) {
        requestUtils("检查用户", "get", checkUserApi, "", false).then();
    }
}

export function getLocalAuthCode() {
    return localAuthToken;
}

export function getLocalAccount() {
    return localAccount;
}

export function sendAuthCode(account) {
    return new Promise(function (resolve, reject) {
        requestUtils("发送验证码", "get", sendAuthCodeApi + "?account=" + account, {account: account}, true).then(res => {
            resolve(res);
        })
    })
}

export function login(account, authCode) {
    let loginParam = {
        userEmail: account,
        authCode: authCode
    }
    requestUtils("登录", "post", loginApi, loginParam, true).then(resData => {
        localStorage.setItem("authToken", resData.authToken);
        localStorage.setItem("refreshToken", resData.refreshToken);
        localStorage.setItem("authUid", resData.authUid);
        localStorage.setItem("account", account);
        window.location.href = "/";
    })
}

export function logout() {
    let authUid = localStorage.getItem("authUid");
    requestUtils("退出登录", "get", logoutApi + authUid, "", true).then(res => {
        localStorage.removeItem("authToken");
        localStorage.removeItem("refreshToken");
        localStorage.removeItem("authUid");
        toLoginPage();
    })
}

export function toLoginPage() {
    localStorage.removeItem("authToken");
    localStorage.removeItem("refreshToken");
    localStorage.removeItem("authUid");
    if (window.location.href !== curLoginPath) {
        window.location.href = "/#/user/login";
    }
}