import {handleUndefined} from "./StringUtils";
import $ from 'jquery';
import {getLocalText} from "./Language";

export function sysToast(msg, type, second, sound) {
    if (!second) {
        second = 1;
    }
    let typeIcon;
    let animateClass = "";
    sound = handleUndefined(sound, false);
    switch (type) {
        case "success":
            animateClass = " animate__animated animate__rubberBand";
            typeIcon = "<i style='color: green' class='fa fa-check-circle'></i>"
            break;
        case "warn":
            animateClass = " animate__animated animate__heartBeat";
            typeIcon = "<i style='color: orange' class='fa fa-exclamation-triangle'></i>"
            break;
        case "error":
            animateClass = " animate__animated animate__shakeX";
            typeIcon = "<i style='color: red' class='fa fa-window-close'></i>"
            break;
        case "loading":
            animateClass = " animate__animated animate__rotateIn";
            animateClass = " animate-circle";
            typeIcon = "<i style='color: yellow' class='fa fa-spinner'></i>"
            break;
        default:
            animateClass = " animate__animated animate__pulse";
            typeIcon = "<i style='color:var(--primary-color)' class='fa fa-info-circle'></i>"
            break;
    }
    $(".sys-toast-mask").remove();
    let msgHtml = "";
    if (type === "loading") {
        msgHtml =
            "<div class='sys-toast-mask loading'>" +
            "   <div class='sys-toast-msg'>" +
            "       <div class='sys-toast-icon" + animateClass + "'>" + typeIcon + "</div>" +
            "       <div class='sys-toast-text'>" + msg + "</div>" +
            "   </div>" +
            "</div>";
    } else {
        msgHtml =
            "<div class='sys-toast-mask'>" +
            "   <div class='sys-toast-msg'>" +
            "       <div class='sys-toast-icon" + animateClass + "'>" + typeIcon + "</div>" +
            "       <div class='sys-toast-text'>" + msg + "</div>" +
            "   </div>" +
            "</div>";
    }
    if (type === 'loading') {
        $(msgHtml).appendTo($(document.body));
    } else {
        $(msgHtml).appendTo($(document.body)).delay(1000 * second).fadeOut();
    }
    // if (type !== "loading") {
    //     $(".smart-msg-mid").removeClass("loadingBl");
    // } else {
    //     $(".smart-msg-mid").addClass("loadingBl");
    // }
    // $(".smart-msg-mid").css("height", "150px");
    // $(".smart-msg-mid").css("width", "150px");
    // setTimeout(function () {
    //     $(".smart-msg-mid").html(
    //         "   <div class='sys-toast-msg'>" +
    //         "       <div class='sys-toast-icon" + animateClass + "'>" + typeIcon + "</div>" +
    //         "       <div class='sys-toast-text'>" + msg + "</div>" +
    //         "   </div>");
    // }, 500);
    // if (type !== "loading") {
    //     setTimeout(function () {
    //         closeSmartMsg();
    //     }, 1000 * second + 500);
    // }
}

function closeSmartMsg() {
    $(".smart-msg-mid").css("height", "15px");
    $(".smart-msg-mid").css("width", "15px");
    $(".smart-msg-mid").html("");
}

export function showSysLoading() {
    sysToast(getLocalText('load_def_text') + "...", "loading");
}

export function closeSysLoading() {
    $(".loading").remove();
    $(".loadingBl").css("height", "0px");
    $(".loadingBl").css("width", "0px");
    $(".loadingBl").html("");
}

export function showMsgList() {
    $(".smart-msg-mid").css("height", "30vh");
    $(".smart-msg-mid").css("width", "90vw");
    $(".smart-msg-mid").html("<div class='close-smart-msg-btn'><i class=\"fa fa-times\"></i></div>");
    $(".close-smart-msg-btn").click(function () {
        closeSmartMsg();
    })
}