import $ from "jquery";
import {handleUndefined} from "./StringUtils";

export function showPopup(content) {
    $(".sys-popup-mask").remove();
    const msgHtml =
        "<div class='sys-popup'>" +
        "<div class='sys-popup-mask'></div>  " +
        "<div class='sys-popup-main animate__animated animate__slideInUp'>" +
        "       <div class='sys-popup-content'>" + content + "</div>" +
        "   </div>" +
        "</div>";
    $(msgHtml).appendTo($(document.body)).fadeIn();
    $(".sys-popup-mask").click(function () {
        $(".sys-popup-mask").fadeOut();
    })
    $(".sys-popup").click(function (e) {
        e.stopPropagation();
    })
    $(".sys-popup-head").click(function (e) {
        $(".sys-popup-mask").fadeOut();
        e.stopPropagation();
    })
}


export function sysConfirm(props) {
    $(".confirm-div").remove();
    let confirm = handleUndefined(props.confirm);
    let confirmText = handleUndefined(props.confirmText, "确定吗？");
    const msgHtml =
        "<div class='sys-popup-mask confirm-div'>" +
        "   <div class='confirm-popup sys-popup animate__animated animate__slideInUp'>" +
        "       <div class='sys-confirm-head'>" + confirmText + "</div>" +
        "       <div class='sys-confirm-popup-content' style='min-height: 0px;'>" +
        "           <div class='sys-confirm-btn sys-btn'>确定</div>" +
        "           <div class='sys-cancel-btn sys-btn-blank'>取消</div>" +
        "       </div>" +
        "   </div>" +
        "</div>";
    $(msgHtml).appendTo($(document.body)).fadeIn();
    $(".confirm-div").click(function () {
        hideConfirm();
    })
    $(".confirm-popup").click(function (e) {
        e.stopPropagation();
    })
    $(".sys-cancel-btn").click(function (e) {
        hideConfirm();
        e.stopPropagation();
    })
    $(".sys-confirm-btn").click(function (e) {
        hideConfirm();
        confirm();
        e.stopPropagation();
    })
}

function hideConfirm() {
    $(".sys-popup-mask.confirm-div").fadeOut();
}