import {Canvas, Children} from '@antv/f2';


export function dataCircleChart(id, dataV, imgs, colors) {
    const context = document.getElementById(id).getContext('2d');
    // 数据集
    let data = [];

    const produceData = (count) => {
        for (let i = 0; i < count; i++) {
            data[i] = [
                {
                    time: dataV[0],
                    color: colors[0],
                    bgColor: 'gray',
                    icon: imgs[0],
                },
                {
                    time: dataV[1],
                    color: colors[1],
                    bgColor: 'gray',
                    icon: imgs[1],
                },
                {
                    time: dataV[2],
                    color: colors[2],
                    bgColor: 'gray',
                    icon: imgs[2],
                },
            ];
        }
        return data;
    };
    data = produceData(8);
    //#endregion

    /**
     * 设置
     */
    const rUnit = 20;
    const gap = 1;
    const bgArcWidth = rUnit - gap;
    const arcWidth = bgArcWidth - gap;

    //#region 图表
    // 外壳组件
    const Shell = (props, context) => {
        const {children, data} = props;
        const {left, top, width, height} = context;
        const centerX = left + width / 2;
        const centerY = top + height / 2;
        return Children.map(children, (child) => {
            return Children.cloneElement(child, {
                data,
                centerX,
                centerY,
                rUnit,
            });
        });
    };

    // 背景组件
    const ArcGuideGroup = (props) => {
        const {attrs} = props;
        const {data, centerX, centerY, rUnit} = props;
        return (
            <group>
                {data.map((record, index) => {
                    return (
                        <arc
                            key={'arc_' + index}
                            attrs={{
                                ...attrs,
                                x: centerX,
                                y: centerY,
                                r: rUnit * (1 * index + 1),
                                stroke: record.bgColor,
                            }}
                        />
                    );
                })}
            </group>
        );
    };

    // 弧线组件
    const RoundArcGroup = (props) => {
        const {data, centerX, centerY, rUnit, attrs, animation} = props;
        return (
            <group>
                {data.map((record, index) => {
                    return (
                        <arc
                            key={'record_' + index}
                            attrs={{
                                ...attrs,
                                x: centerX,
                                y: centerY,
                                r: (1 * index + 1) * rUnit,
                                stroke: record.color,
                                startAngle: Math.PI / -2,
                                endAngle: record.time * 2 * Math.PI - Math.PI / 2,
                            }}
                            // animation={{...animation}}
                        />
                    );
                })}
            </group>
        );
    };

    // icon组件
    const ImgGuideGroup = (props) => {
        const {data, centerX, centerY, rUnit, attrs} = props;
        const {width, height} = attrs;
        return (
            <group>
                {data.map((record, index) => {
                    return (
                        <image
                            cacheImage
                            key={'img_' + index}
                            attrs={{
                                ...attrs,
                                width,
                                height,
                                x: centerX - width / 2,
                                y: centerY - (1 * index + 1) * rUnit - height / 2,
                                src: record.icon,
                            }}
                        />
                    );
                })}
            </group>
        );
    };

    // apple-watch
    const {props} = (
        <Canvas context={context} pixelRatio={window.devicePixelRatio}>
            {Object.keys(data).map((iter) => {
                const records = data[iter];
                return (
                    <Shell key={'shell_' + iter} context={context} data={records}>
                        <ArcGuideGroup
                            attrs={{
                                lineWidth: bgArcWidth,
                                opacity: 0.9,
                            }}
                        />
                        <RoundArcGroup
                            attrs={{
                                lineWidth: arcWidth,
                                lineCap: 'round',
                                shadowOffsetX: -2,
                                shadowOffsetY: -5,
                                shadowBlur: 50,
                            }}
                            animation={{
                                appear: {
                                    duration: 1500,
                                    easing: 'elasticOut',
                                    property: ['endAngle'],
                                },
                                update: {
                                    duration: 1500,
                                    easing: 'elasticOut',
                                    property: ['endAngle'],
                                },
                            }}
                        />
                        <ImgGuideGroup
                            attrs={{
                                width: 16,
                                height: 16,
                            }}
                        />
                    </Shell>
                );
            })}
        </Canvas>
    );
    //#endregion
    const canvas = new Canvas(props);
    canvas.render();
}
