import SysHeader from "../../../component/SysHeader";
import React, {useEffect, useState} from "react";
import SysMain from "../../../component/SysMain";
import {useNavigate} from "react-router-dom";
import SysButton from "../../../component/SysButton";
import SysPopup from "../../../component/SysPopup";
import SysInput from "../../../component/SysInput";
import {addData, delDataById, getDataByType} from "../../../js/DataUtils";
import $ from 'jquery'
import {sysToast} from "../../../js/MessageUtils";
import {isBlank, isTime} from "../../../js/StringUtils";
import {BookOpen, ChopsticksFork, Left, Palm, Platte, Plus, Sleep, Sport} from "@icon-park/react";
import SysListItem from "../../../component/SysListItem";
import {sysConfirm} from "../../../js/PopupUtils";
import SysEmpty from "../../../component/SysEmpty";
import SysSelect from "../../../component/SysSelect";
import SysListSkeleton from "../../../component/SysListSkeleton";
import SysPullToRefresh from "../../../component/SysPullToRefresh";

function ToDoMain() {
    const [defTodoName, setDefTodoName] = useState("");
    const [defTodoTime, setDefTodoTime] = useState("");
    const [defTodoType, setDefTodoType] = useState("sm");
    const [defTodoTimeType, setDefTodoTimeType] = useState("mt");
    const [showAddTodo, setShowAddTodo] = useState(false);
    const [todoData, setToDoData] = useState(<SysListSkeleton/>);
    const navigate = useNavigate();
    const headerOption = {
        left: <Left/>,
        leftClick: () => goPage("/app/todo"),
        right: <Plus/>,
        rightClick: () => toAddTodoItem(),
    };
    useEffect(() => {
        initData();
    }, []);

    function initData() {
        setToDoData(<SysListSkeleton/>);
        getTodoItem("todo-item").then(smTodoOption => {
            let reqBData = [];
            smTodoOption.forEach((item, index) => {
                reqBData.push(item);
            })
            if (reqBData.length > 0) {
                setToDoData(reqBData);
            } else {
                setToDoData(<SysEmpty/>);
            }
        });
    }

    function goPage(page) {
        navigate(page);
    }

    function toAddTodoItem() {
        setDefTodoName("");
        setDefTodoTime("");
        setDefTodoTimeType("mt");
        setDefTodoType("sm");
        setShowAddTodo(true);
    }

    function addTodoItem() {
        let todoType = $("#todoType").val();
        let todoTimeType = $("#todoTimeType").val();
        if (isBlank(todoType) || isBlank(defTodoName) || isBlank(defTodoTime) || isBlank(todoTimeType)) {
            sysToast("存在空项", "warn");
            return;
        } else {
            if (!isTime(defTodoTime)) {
                sysToast("时间格式不正确", "warn");
                return;
            }
            let data = {
                todoType: todoType,
                todoName: defTodoName,
                todoTime: defTodoTime,
                todoTimeType: todoTimeType
            };
            addData("todo-item", data).then(res => {
                setShowAddTodo(false);
                sysToast("添加成功", "success");
                initData();
            });
        }
    }

    function getTodoItem(todoType) {
        return new Promise(function (resolve, reject) {
            getDataByType(todoType).then(resData => {
                let data = [];
                resData.forEach((item, index) => {
                    let todoTypeIcon = "";
                    let todoTimeType = "";
                    let todoData = JSON.parse(item.data);
                    switch (todoData.todoType) {
                        case "sm":
                            todoTypeIcon = <Sleep/>;
                            break;
                        case "ys":
                            todoTypeIcon = <ChopsticksFork/>;
                            break;
                        case "yd":
                            todoTypeIcon = <Sport/>;
                            break;
                        case "xx":
                            todoTypeIcon = <BookOpen/>;
                            break;
                        case "xq":
                            todoTypeIcon = <Platte/>;
                            break;
                        case "sj":
                            todoTypeIcon = <Palm/>;
                            break;
                        default:
                            break;
                    }
                    switch (todoData.todoTimeType) {
                        case "mt":
                            todoTimeType = "每天";
                            break;
                        case "gzr":
                            todoTimeType = "工作日";
                            break;
                        case "z6":
                            todoTimeType = "周六";
                            break;
                        case "zr":
                            todoTimeType = "周日";
                            break;
                        default:
                            todoTimeType = "";
                            break;
                    }
                    let todoItem = <SysListItem swipeMenu={true} swipeMenuList={[
                        {
                            key: 'delete',
                            text: '删除',
                            color: 'danger',
                            onClick: async () => {
                                sysConfirm({
                                    confirm: function () {
                                        delDataById(item.dataId).then(res => {
                                            if (res) {
                                                sysToast("删除成功", "success");
                                                initData();
                                            }
                                        });
                                    }
                                });
                            },
                        },
                    ]} key={todoData.todoName + todoData.todoTime} icon={<span>{todoTypeIcon}</span>}
                                                content={
                                                    <span>{todoData.todoName + " " + todoTimeType + " " + todoData.todoTime}</span>}/>;
                    data.push(todoItem);
                })
                resolve(data);
            });
        });
    }

    return (
        <div>
            <SysHeader option={headerOption}/>
            <SysMain className="top-main">
                <SysPullToRefresh onRefresh={() => initData()}>
                    <div style={{padding: "10px"}}>
                        {todoData}
                    </div>
                </SysPullToRefresh>
                <SysPopup title="添加" show={showAddTodo} setShowPopup={setShowAddTodo}>
                    <SysSelect id="todoType" title="类型" defaultValue={defTodoType} setDefaultValue={setDefTodoType}
                               options={["sm-睡眠", "ys-饮食", "yd-运动", "xx-学习", "xq-兴趣", "sj-实践"]}/>
                    <SysInput id="todoName" title="名称" defaultValue={defTodoName}
                              setDefaultValue={setDefTodoName}/>
                    <SysInput id="todoTime" title="时间" defaultValue={defTodoTime}
                              setDefaultValue={setDefTodoTime}/>
                    <SysSelect id="todoTimeType" title="周期" defaultValue={defTodoTimeType}
                               setDefaultValue={setDefTodoTimeType}
                               options={["mt-每天", "gzr-工作日", "z6-周六", "zr-周日"]}/>
                    <SysButton text="确定" click={() => addTodoItem()}/>
                </SysPopup>
            </SysMain>
        </div>
    );
}

export default ToDoMain;
