import React from "react";
import {handleUndefined} from "../js/StringUtils";

function SysTab(props) {
    let tabList = handleUndefined(props.tabList, []);
    let defTab = handleUndefined(props.defTab, 0);
    let tabContent = handleUndefined(props.tabContent, "");
    let change = handleUndefined(props.change, undefined);
    return (
        <div>
            <div className="sys-tab">
                {
                    tabList.map((item, index) => (
                        <div key={item.split("-")[0]}
                             className={index === defTab ? "sys-tab-item-active" : "sys-tab-item"}
                             onClick={() => change(index)}>{item.split("-")[1]}</div>
                    ))
                }
            </div>
            <div className="sys-tab-content">
                {tabContent}
            </div>
        </div>
    );
}

export default SysTab;