import {handleUndefined} from "../js/StringUtils";
import React, {AnchorHTMLAttributes, ButtonHTMLAttributes, FC} from "react";
import $ from 'jquery'

interface BaseComponentProps {
    children: React.ReactNode;
}

type NativeButtonProps = BaseComponentProps & ButtonHTMLAttributes<HTMLElement>
type AnchorButtonProps = BaseComponentProps & AnchorHTMLAttributes<HTMLElement>
export type ComponentProps = Partial<NativeButtonProps & AnchorButtonProps>

export const SysPopup: FC<ComponentProps> = (props) => {
    let show = handleUndefined(props.show, false);
    let setShowPopup = handleUndefined(props.setShowPopup, undefined);
    let title = handleUndefined(props.title, "");
    const {
        children,
    } = props
    $(".sys-popup").addClass("animate__slideInUp");
    $(".sys-popup").removeClass("animate__slideOutDown");
    return (
        <div>
            <div className='sys-popup-mask' style={{display: show ? "block" : "none"}}
                 onClick={() => {
                     $(".sys-popup").removeClass("animate__slideInUp");
                     $(".sys-popup").addClass("animate__slideOutDown");
                     setTimeout(function () {
                         setShowPopup(false);
                     }, 200);
                 }}>
                <div className='sys-popup animate__animated animate__slideInUp'>
                    <div className='sys-popup-head'>{title}</div>
                    <div className='sys-popup-content' onClick={(e) => {
                        e.stopPropagation();
                    }}>{children}</div>
                </div>
            </div>
        </div>
    );
}
export default SysPopup;